html,
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  height: 100vh;
  text-align: center;
  margin-top: 0;
}
.header {
  height: 5rem;
  width: 100%;
}
.header h1 {
  margin: 0;
  width: 100%;
  padding: 2vh 0;
  background-color: #00bcd4;
  color: white;
}

.tasks-container {
  display: flex;
  height: 70vh;
  justify-content: center;
  transition: all 0.3s ease;
}

.task-container {
  box-shadow: 10px 10px 10px 5px rgb(71, 72, 73);
  padding: 0.5rem;
  margin: 15px auto;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: lightblue;
  transition: all 0.3s ease;
  max-width: 70vw;
}
.task-container h1 {
  margin: 0px;
  padding: 0px;
  text-transform: uppercase;
  font-size: 1rem;
}
.task-container p {
  margin-top: none;
  color: #00bcd4;
  font-weight: 800;
}

#form-container {
  position: sticky;
  top: 0;
  z-index: 2;
  border-bottom: 2px solid gray;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
}
form input {
  border: 2px solid #00bcd4;
  border-radius: 5px;
  background-color: lightblue;
  text-align: center;
  color: black;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 10px;
  border: none;
}

form button {
  background-color: white;
  font-size: 1rem;
  border-radius: 5px;
  color: #00bcd4;
  border: none;
  margin-bottom: 5px;
}
.task-container button {
  border: none;
  visibility: hidden;
  background: none;
  font-size: 0.8rem;
  height: 0px;
  color: red;
  font-weight: 500;
  margin-right: 8px;
}

.task-container:hover > div > button {
  visibility: visible;
  height: auto;
}

.danger {
  position: sticky;
  top: 25%;
  background-color: rgb(238, 92, 92);
  color: red;
  width: 50vw;
  margin: 0px auto;
  font-size: 1rem;
  border-radius: 50px;
  padding: 5px;
  font-weight: 800;
  margin-bottom: none;
}

.sucess {
  position: sticky;
  top: 25%;
  background-color: rgb(66, 136, 66);
  color: rgb(0, 255, 0);
  width: 50vw;
  margin: 0px auto;
  font-size: 1rem;
  border-radius: 50px;
  padding: 5px;
  font-weight: 800;
  margin-bottom: none;
}
.btn-container {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  border-top: 3px solid gray;
  padding-top: 5px;
  align-self: center;
  transition: all 0.3s ease;
}
.time {
  position: relative;
  top: -28px;
  background-color: rgb(66, 136, 66);
  color: rgb(0, 255, 0);
  font-size: 1rem;
  font-weight: 500;
  width: 100%;
  padding: 5px;
  margin-bottom: none;
}
